@media (max-width: 767px) {
  .feature {
    grid-template-columns: minmax(0, 1fr);
    row-gap: 30px;
  }
  .feature .feature__img {
    justify-self: center;
  }
}
.feature--img-right .feature__img {
  grid-column: 2;
  grid-row: 1;
}
@media (max-width: 767px) {
  .feature--img-right .feature__img {
    grid-column: auto;
    grid-row: auto;
  }
}
.feature__main-title {
  margin-bottom: 12px;
  font-weight: 800;
  font-size: 36px;
  line-height: 1.2;
  letter-spacing: -0.01em;
  color: #111928;
}
@media (max-width: 767px) {
  .feature__main-title {
    font-size: 30px;
    text-align: center;
  }
}
.feature__title {
  margin-bottom: 20px;
  font-weight: 800;
  font-size: 24px;
  line-height: 1.38;
  letter-spacing: -0.03em;
  color: #1b1c31;
}
.feature__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  letter-spacing: 0.01em;
  color: #000c2d;
}
.feature__list--margin-size-l .feature__item {
  margin-top: 16px;
}
.feature__list--margin-size-l .feature__item:first-child {
  margin-top: 0;
}
.feature__item {
  position: relative;
  padding-left: 26px;
}
.feature__icon {
  --icon-size: 16px;
  position: absolute;
  top: 2px;
  left: 0;
  width: var(--icon-size);
  height: var(--icon-size);
}
.feature__text + .feature__list {
  margin-top: 16px;
}
.feature__text, .feature__description {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  letter-spacing: 0.01em;
  color: #000C2d;
}
.feature__text--size-m {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  color: rgba(0, 0, 0, 0.55);
}
@media (max-width: 767px) {
  .feature__text--size-m {
    text-align: center;
  }
}
.feature__description {
  grid-column: 1/-1;
  justify-self: center;
}
.feature__img {
  justify-self: center;
}
@media (max-width: 767px) {
  .feature__img--bottom {
    grid-row: 2;
  }
}

.agreement {
  padding: 70px 0;
}
.agreement__wrapper {
  display: grid;
  max-width: 1300px;
  margin: 0 auto;
}
.agreement__text {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  letter-spacing: 0.15px;
  color: #1b1b1b;
}
.agreement ol {
  margin: 0;
  padding: 0;
  counter-reset: item;
}
.agreement li {
  display: block;
}
.agreement li::before {
  content: counters(item, ".") " ";
  counter-increment: item;
}
.agreement .agreement_back-button {
  justify-self: center;
  padding: 16px 25px;
  margin-top: 60px;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: -0.01em;
  border-radius: 12px;
}

.examples {
  padding: 35px 0 50px;
  overflow: hidden;
}
.examples .title {
  max-width: 1000px;
  margin: 0 auto;
  margin-bottom: 35px;
  font-weight: 800;
  font-size: 36px;
  line-height: 1.25;
  letter-spacing: -0.01em;
  color: #111928;
}
.examples__slider-heading {
  overflow: auto;
}
.examples__slider-heading img {
  max-width: none;
  width: auto;
  height: 45px;
}

.slider__container {
  padding: 40px 0 50px;
  overflow: visible;
}
.slider__wrapper {
  place-items: center;
}
.slider .container {
  display: grid;
  place-items: center;
}
.slider__pagination {
  bottom: 0;
}
.slider .swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  background: #7837e6;
  border-radius: 50%;
  opacity: 1;
}
.slider .swiper-pagination-bullet-active {
  width: 44px;
  height: 8px;
  background: #9759bd;
  border-radius: 4px;
}

.about-intro {
  position: relative;
  padding: 100px 0 80px;
  background-color: rgba(0, 255, 133, 0.08);
  border-bottom: 5px solid #f5f5f5;
}
.about-intro__decor-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 30%;
  left: 0;
  overflow: hidden;
}
@media (max-width: 2400px) {
  .about-intro__decor-container {
    bottom: 37%;
  }
}
.about-intro__decor {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: hidden;
}
@media (max-width: 1600px) {
  .about-intro__decor {
    top: 0;
  }
}
.about-intro__decor img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about-intro .container {
  position: relative;
}
.about-intro__wrapper {
  display: grid;
}
.about-intro__content {
  display: grid;
  grid-template-columns: minmax(0, 1fr) auto minmax(0, 1fr);
  column-gap: clamp(20px, 4vw, 40px);
  align-items: center;
  justify-items: end;
  row-gap: 30px;
}
@media (max-width: 1023px) {
  .about-intro__content {
    grid-template-columns: minmax(0, 1fr);
    justify-items: center;
    row-gap: 60px;
  }
}
.about-intro__title {
  grid-column: 1/-1;
  max-width: 900px;
  margin: 0 auto;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.8;
  text-align: center;
  color: #ffffff;
}
@media (max-width: 767px) {
  .about-intro__title {
    font-size: 25px;
  }
}
.about-intro__img-left {
  max-width: 390px;
  transform: translateY(25%) scale(1.2);
}
@media (max-width: 1023px) {
  .about-intro__img-left {
    grid-row: 3;
    transform: translateY(0) scale(1);
  }
}
.about-intro__img-right {
  max-width: 340px;
  transform: translateY(-45%);
}
@media (max-width: 1023px) {
  .about-intro__img-right {
    grid-row: 1;
    transform: translateY(0);
  }
}
.about-intro__button {
  padding: 16px 25px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.02em;
  transform: translateY(-20%);
}
@media (max-width: 1023px) {
  .about-intro__button {
    transform: translateY(0);
  }
}
.about-intro__list {
  margin: 0;
  margin-top: 100px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: clamp(20px, 5vw, 50px);
}
.about-intro__item {
  display: grid;
  align-content: start;
  justify-items: center;
  max-width: 260px;
}
.about-intro__icon {
  width: 100px;
  height: 100px;
  padding: 27px;
  background-color: rgba(47, 112, 254, 0.1);
  border-radius: 50%;
}
.about-intro__icon svg {
  width: 100%;
  height: 100%;
}
.about-intro__item:nth-child(even) .about-intro__icon {
  background-color: rgba(109, 237, 2, 0.1);
}
.about-intro__text {
  margin-top: 15px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.7;
  text-align: center;
  letter-spacing: 0.01em;
  color: #000c2d;
}
.about-intro__footer {
  position: relative;
}
.about-intro__footer-decor {
  position: absolute;
  width: clamp(80px, 6vw, 120px);
}
@media (max-width: 767px) {
  .about-intro__footer-decor {
    display: none;
  }
}
.about-intro__footer-decor--left {
  top: 90%;
  left: 0;
}
.about-intro__footer-decor--right {
  top: 10%;
  right: 0;
}

